import "./styles.css";
import {
  Grommet,
  Header,
  Text,
  Clock,
  Collapsible,
  Button,
  Box,
  Grid,
} from "grommet";
import React from "react";

export default function App() {
  const [open, setOpen] = React.useState(false);

  return (
    <Grommet full theme={theme} className="main">
      <Box align="start">
        <Button primary onClick={() => setOpen(!open)} label="Toggle" />
        <Collapsible open={open}>
          <Box
            background="light-2"
            round="medium"
            pad="medium"
            align="center"
            justify="center"
          >
            <Text>This is a box inside a Collapsible component</Text>
          </Box>
        </Collapsible>
      </Box>
      <Clock type="digital" />
      <Clock type="digital" />
    </Grommet>
  );
}

const theme = {
  global: {
    font: {
      family: "Roboto",
      size: "18px",
      height: "20px",
    },
  },
};
